/* Top.css */
.top-container {
    display: flex;
    /* Flexbox를 사용하여 수평으로 정렬 */
    align-items: center;
    /* 수직 중앙 정렬 */
    margin: 1rem 2.8rem;
}

.top-container img {
    vertical-align: middle;
    margin-right: 4rem;
    padding-top: 0.6rem;
}

.top-container a {
    margin-right: 4rem;
    display: inline;
    text-decoration: none;
    color: black;
    line-height: 1;
    font-size: 1.3rem;
    line-height: 1.5;
    letter-spacing: 0.1rem;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 500;
}

.logo-container {
    position: relative;
    width: 100%;
    max-width: 1920px;
}

.logo-container img.logo1 {
    position: absolute;
    width: 12rem;
    height: auto;
    animation: moveAround 5s infinite ease-in-out;

}

.logo-container img.logo2 {
    position: absolute;
    width: 12rem;
    height: auto;
    margin-top: 14rem;
    animation: moveAround 5s infinite ease-in-out;
    animation-delay: 2.5s;
}

.logo-container img.logo3 {
    position: absolute;
    width: 12rem;
    height: auto;
    margin-top: 28rem;
    animation: moveAround 5s infinite ease-in-out;

}

@keyframes moveAround {
    0% {
        top: 0;
        left: 2%;
    }

    25% {
        top: 0;
        left: 83%;
    }

    50% {
        top: 0;
        left: 83%;
    }

    75% {
        top: 0;
        left: 2%;
    }

    100% {
        top: 0;
        left: 2%;
    }
}

.tab {
    animation: moveAround 5s infinite ease-in-out;
}